/* eslint-disable camelcase */
import {
    Component,
    h,
    Prop,
    State,
    EventEmitter,
    Event,
    Listen,
    Watch,
} from '@stencil/core';
import {
    LimeWebComponentPlatform,
    LimeWebComponentContext,
} from '@limetech/lime-web-components';
import {
    fetchTemplates,
    fetchLimeDocuments,
    fetchTemplateFields,
    fetchObjectProps,
    fetchTemplateRoles,
} from '../../services';
import { ISession } from '../../types/Session';
import { ICustomField } from '../../types/CustomField';
import { IListItem } from '../../types/ListItem';
import { Column, Tab } from '@limetech/lime-elements';
import { ITemplateRole } from 'src/types/TemplateRole';

enum EnumSections {
    None = 'none',
    Template = 'template',
    LimeDocuments = 'limeDocuments',
}

type TableRow = {
    field: string;
    value: string;
};

@Component({
    tag: 'layout-select-file',
    shadow: true,
    styleUrl: 'layout-select-file.scss',
})
export class LayoutSelectFile {
    @Prop() public platform: LimeWebComponentPlatform;
    @Prop() public context: LimeWebComponentContext;
    @Prop() public session: ISession;
    @Prop() public selectedTemplate: IListItem;
    @Prop() public selectedLimeDocument: IListItem;
    @Prop() public customFields: ICustomField[] = [];
    @Prop() public templateRoles: ITemplateRole[] = [];

    @Event() setCustomFields: EventEmitter;
    @Event() setTemplateRoles: EventEmitter;
    @Event() private errorHandler: EventEmitter<string>;

    @State() private isLoadingTemplates: boolean = false;
    @State() private isLoadingProps: boolean = false;
    @State() private templates: IListItem[] = [];

    @State() private isLoadingFields: boolean = false;
    @State() private isLoadingLimeDocuments: boolean;
    @State() private limeDocuments: IListItem[];

    @State() private openSection: EnumSections = EnumSections.Template;

    @State() private tableData: TableRow[] = [];

    @State()
    private columns: Column[] = [
        { title: 'Merge tag', field: 'field' },
        { title: 'Value', field: 'value' },
    ];

    @State()
    private tabs: Tab[] = [
        {
            id: 'merge-fields',
            text: 'Template fields',
            active: true,
        },
        {
            id: 'getaccept-merge-fields',
            text: 'GetAccept template fields',
        },
    ];

    @State()
    private gaMergeFieldColumns: Column[] = [
        { title: 'Merge tag', field: 'field' },
        { title: 'Label', field: 'value' },
    ];

    @State()
    private gaMergeFields: TableRow[] = [
        {
            field: '{{recipient.first_name}}',
            value: 'Recipient - First name',
        },
        {
            field: '{{recipient.last_name}}',
            value: 'Recipient - Last name',
        },
        {
            field: '{{recipient.email}}',
            value: 'Recipient - Email',
        },
        {
            field: '{{recipient.company_name}}',
            value: 'Recipient - Company name',
        },
        {
            field: '{{sender.first_name}}',
            value: 'Sender - First name',
        },
        {
            field: '{{sender.last_name}}',
            value: 'Sender - Last name',
        },
        {
            field: '{{sender.fullname}}',
            value: 'Sender - Full name',
        },
        {
            field: '{{document.name}}',
            value: 'Document - Name',
        },
        {
            field: '{{document.field}}',
            value: 'Document - field',
        },
    ];

    constructor() {
        this.loadTemplates = this.loadTemplates.bind(this);
        this.loadTemplateFields = this.loadTemplateFields.bind(this);
        this.loadLimeDocuments = this.loadLimeDocuments.bind(this);
        this.onChangeSection = this.onChangeSection.bind(this);
        this.setTemplates = this.setTemplates.bind(this);
        this.setLimeDocuments = this.setLimeDocuments.bind(this);
        this.setFields = this.setFields.bind(this);
        this.onActivateRow = this.onActivateRow.bind(this);
        this.loadTemplateRoles = this.loadTemplateRoles.bind(this);
    }

    public render() {
        return [
            <div class="layout-select-file-container">
                <h3>Select file to send</h3>
                <div class="select-file-container">
                    <div class="file-column">
                        <limel-collapsible-section
                            header="Templates"
                            isOpen={this.openSection === EnumSections.Template}
                            onOpen={event =>
                                this.onChangeSection(
                                    event,
                                    EnumSections.Template
                                )
                            }
                            onClose={event =>
                                this.onChangeSection(event, EnumSections.None)
                            }
                        >
                            <template-list
                                templates={this.templates}
                                selectedTemplate={this.selectedTemplate}
                                isLoading={this.isLoadingTemplates}
                            />
                        </limel-collapsible-section>
                        <limel-collapsible-section
                            header="Lime documents"
                            isOpen={
                                this.openSection === EnumSections.LimeDocuments
                            }
                            onOpen={event =>
                                this.onChangeSection(
                                    event,
                                    EnumSections.LimeDocuments
                                )
                            }
                            onClose={event =>
                                this.onChangeSection(event, EnumSections.None)
                            }
                        >
                            <lime-document-list
                                documents={this.limeDocuments}
                                selectedLimeDocument={this.selectedLimeDocument}
                                isLoading={this.isLoadingLimeDocuments}
                            />
                        </limel-collapsible-section>
                    </div>
                    <div class="file-column">
                        <template-preview
                            template={this.selectedTemplate}
                            isLoading={this.isLoadingFields}
                            session={this.session}
                        />
                        <custom-fields
                            template={this.selectedTemplate}
                            customFields={this.customFields}
                            isLoading={this.isLoadingFields}
                        />
                    </div>
                </div>
                {!this.isLoadingProps ? (
                    <limel-collapsible-section
                        header="Show template parameters"
                        onClose={event => event.stopPropagation()}
                    >
                        <limel-tab-panel
                            tabs={this.tabs}
                            onChangeTab={this.handleChangeTab}
                        >
                            <limel-table
                                mode="remote"
                                data={
                                    this.tableData.sort((a, b) =>
                                        a.field.localeCompare(b.field)
                                    ) || []
                                }
                                columns={this.columns}
                                onActivate={this.onActivateRow}
                                id="merge-fields"
                            />
                            <limel-table
                                mode="local"
                                data={this.gaMergeFields.sort((a, b) =>
                                    a.field.localeCompare(b.field)
                                )}
                                columns={this.gaMergeFieldColumns}
                                onActivate={this.onActivateRow}
                                id="getaccept-merge-fields"
                            />
                        </limel-tab-panel>
                    </limel-collapsible-section>
                ) : (
                    <limel-spinner />
                )}
            </div>,
        ];
    }

    public async componentWillLoad() {
        this.loadTemplates();
        this.loadLimeDocuments();
        this.loadObjectProps();
    }

    private onChangeSection(event, section: EnumSections) {
        event.stopPropagation();
        this.openSection = section;
    }

    private async loadTemplates() {
        this.isLoadingTemplates = true;

        try {
            this.templates = await fetchTemplates(
                this.platform,
                this.session,
                this.selectedTemplate
            );
        } catch (e) {
            this.errorHandler.emit(
                'Could not load templates from GetAccept...'
            );
        }

        this.isLoadingTemplates = false;
    }

    private async loadLimeDocuments() {
        this.isLoadingLimeDocuments = true;
        const { id: record_id, limetype } = this.context;

        try {
            this.limeDocuments = await fetchLimeDocuments(
                this.platform,
                limetype,
                record_id,
                this.selectedLimeDocument
            );
        } catch (e) {
            this.errorHandler.emit('Could not load related Lime documents...');
        }

        this.isLoadingLimeDocuments = false;
    }

    private handleChangeTab = (event: CustomEvent<Tab>) => {
        this.tabs = this.tabs.map(tab => {
            if (tab.id === event.detail.id) {
                return event.detail;
            }

            return tab;
        });
    };

    private async loadTemplateFields() {
        if (!this.selectedTemplate) {
            this.customFields = [];
            this.setCustomFields.emit(this.customFields);

            return;
        }

        this.isLoadingFields = true;
        const { id: record_id, limetype } = this.context;

        try {
            const fields = await fetchTemplateFields(
                this.platform,
                this.session,
                limetype,
                record_id,
                this.selectedTemplate
            );
            this.setFields(fields);
        } catch (e) {
            this.errorHandler.emit(
                'Could not fetch template fields from GetAccept...'
            );
        }

        this.isLoadingFields = false;
    }

    private async loadObjectProps() {
        const { id: record_id, limetype } = this.context;
        this.isLoadingProps = true;
        try {
            const props = await fetchObjectProps(
                this.platform,
                this.session,
                limetype,
                record_id
            );
            this.setAvailableFields(props);
        } catch (e) {
            this.errorHandler.emit('Could not fetch lime object...');
        }

        this.isLoadingProps = false;
    }

    private async loadTemplateRoles() {
        if (!this.selectedTemplate) {
            return;
        }

        const { id: record_id, limetype } = this.context;

        try {
            const roles = await fetchTemplateRoles(
                this.platform,
                this.session,
                limetype,
                record_id,
                this.selectedTemplate
            );
            this.setRoles(roles);
        } catch (e) {
            this.errorHandler.emit(
                'Could not fetch template roles from GetAccept...'
            );
        }
    }

    private setAvailableFields(limeObject) {
        Object.entries(limeObject).forEach(([key, value]: [string, string]) => {
            if (value) {
                this.tableData.push({
                    field: `{{${key}}}`,
                    value: value,
                });
            }
        });
    }

    private setFields(fields) {
        const customFields: ICustomField[] = fields.map(this.mapField);

        this.setCustomFields.emit(customFields);
    }

    private setRoles(roles: ITemplateRole[]) {
        this.setTemplateRoles.emit(roles);
    }

    @Watch('selectedTemplate')
    onChangeTemplate(data: IListItem) {
        this.setTemplates(data);
        if (data) {
            this.loadTemplateFields();
            this.loadTemplateRoles();
        }
    }

    private setTemplates(template: IListItem) {
        this.templates = this.getSelectedListItems(this.templates, template);
    }

    @Watch('selectedLimeDocument')
    onChangeDocument(data: IListItem) {
        this.setLimeDocuments(data);
    }

    private mapField(field: any): ICustomField {
        return {
            value: field.field_value.toString(),
            id: field.field_id,
            label: field.field_label,
            is_editable: field.is_editable === null ? true : field.is_editable,
        };
    }

    private setLimeDocuments(document: IListItem) {
        this.limeDocuments = this.getSelectedListItems(
            this.limeDocuments,
            document
        );
    }

    private getSelectedListItems(
        items: IListItem[],
        selectedItem: IListItem
    ): IListItem[] {
        return items.map((item: IListItem) => {
            if (selectedItem && item.value === selectedItem.value) {
                return selectedItem;
            }

            return { ...item, selected: false };
        });
    }

    @Listen('updateFieldValue')
    updateFieldValue(event) {
        const { id, value } = event.detail;
        const customFields: ICustomField[] = this.customFields.map(field => {
            return field.id === id ? { ...field, value: value } : field;
        });
        this.setCustomFields.emit(customFields);
    }

    private onActivateRow(event) {
        navigator.clipboard.writeText(event.detail.field);
        this.errorHandler.emit(
            `Copied '${event.detail.field}' to your clipboard`
        );
    }
}
